import React, { useState } from "react";
import Card from "../UI/Card";
import Button from "../UI/Button";
import './AddUser.css';
import ErrorModal from "../UI/ErrorModal";

function AddUser(props) {
  const [enteredUsername, setEnteredUsername] = useState('');
  const [enteredAge, setEnteredAge] = useState('');
  const [error, setError] = useState();

  function usernameChangeHandler(event) {
    setEnteredUsername(event.target.value);
  }

  function ageChangeHandler(event) {
    setEnteredAge(event.target.value);
  }

  function addUserHandler(event) {
    event.preventDefault();
    if (enteredUsername.trim().length === 0 || enteredAge.trim().length === 0) {
      setError({
        title: "Invalid Input",
        message: "Please enter a valid name and age."
      });
      return;
    }
    if (+enteredAge < 0) {
      setError({
        title: "Invalid Age",
        message: "Age must be greater than or equal to 1.",
      });
      return;
    }
    props.onAddUser({ name: enteredUsername, age: enteredAge, id: Math.random().toString() });
    setEnteredUsername('');
    setEnteredAge('');
  }

  function errorHandler() {
    setError(null);
  }

  return (
    <div>
      {error && <ErrorModal title={error.title} message={error.message} onClose={errorHandler} />}
      <Card className="input">
        <form onSubmit={addUserHandler}>
          <label htmlFor="username">Name</label>
          <input id="username" type="text" value={enteredUsername} onChange={usernameChangeHandler} />
          <label htmlFor="age">Age (Years)</label>
          <input id="age" type="number" value={enteredAge} onChange={ageChangeHandler} />
          <Button className="center" type="submit">Add User</Button>
        </form>
      </Card>
      </div>
    );
}

export default AddUser;
